import _ from "lodash";
import { toList } from "../utils/Utils";

export default class TournamentService {

  constructor(firebase) {
    this.firebase = firebase;
  }

  detach() {
    this.firebase.detach();
  }

  save(tournament) {
    this.firebase.save(tournament);
  }

  update(tournamentId, tournament) {
    this.firebase.save(tournamentId, tournament);
  }

  delete(tournamentId) {
    this.firebase.delete(tournamentId);
  }

  list(callback) {
    this.firebase.getTournaments2(callback);
  }

  read(callback, id) {
    this.firebase.getTournament(callback, id);
  }

  listTeams(callback, tournamentId) {
    this.firebase.getTeams(callback, tournamentId);
  }

  listMatches(callback, tournamentId, teamId) {
    this.firebase.getMatches(callback, tournamentId, teamId);
  }

  saveTeam(tournamentId, team) {
    this.firebase.saveTeam(tournamentId, team);
  }

  deleteTeam(tournamentId, teamId) {
    this.firebase.deleteTeam(tournamentId, teamId);
  }

  saveTeams(tournamentId, teams) {
    teams.forEach((team) => {
      this.saveTeam(tournamentId, team);
    });
  }

  saveMatch(tournamentId, teamId, match) {
    this.firebase.saveMatch(tournamentId, teamId, match);
  }

  deleteMatch(tournamentId, teamId, matchId) {
    this.firebase.deleteMatch(tournamentId, teamId, matchId);
  }

  async close(tournamentId) {
    this.firebase.close(tournamentId);
  }

  searchById(list, id) {
    let index = -1;
    list.forEach((item, i) => {
      if(item.id === id){
        index = i;
      }
    })
    return index;
  }

  getMatchPoint(match) {
    return this.getPositionPoint(match.position) + this.getMatchKills(match);
  }

  getMatchKills(match) {
    return _.sum(match.teamScore.map((score) => parseInt(score.kills)));
  }

  getPositionPoint(position) {
    switch(position) {
      case '1': return 20;
      case '2': return 15;
      case '3': return 10;
      case '4': return 5;
      case '5': return 3;
      case '6': return 2;
      default: return 0;
    }
  }

  getKillPoint(kills) {
    return kills;
  }

  getDamagePoint(dmg) {
    return 0;
  }

  getScorePoint(score) {
    return 0;
  }

  getResults(tournament) {
    const players = [...tournament.players];
    const playersResultObject = {};
    players.forEach((player) => {
      if(!playersResultObject[player]){
        playersResultObject[player] = {
          player: player,
          kills: 0,
          damage: 0,
          points: 0,
          total: 0
        }
      }
      Object.keys(tournament.teams).forEach((teamKey) => {
        const team = tournament.teams[teamKey]; 
        if(team.matches){
          Object.keys(team.matches).forEach((matchKey) => {
            const match = team.matches[matchKey];
            if(team.players.indexOf(player) > -1){
              playersResultObject[player]['points'] += this.getPositionPoint(match.position);
              playersResultObject[player]['total'] += this.getPositionPoint(match.position);
            }
            match.teamScore.forEach((score) => {
              if(player === score.player) {
                playersResultObject[player]['kills'] += parseInt(score.kills);
                playersResultObject[player]['damage'] += parseInt(score.damage);
                playersResultObject[player]['total'] += parseInt(score.kills);
              }
            })
          })
        }
      })
    })

    const playersResult = [];
    Object.keys(playersResultObject).forEach((key) => {
      playersResult.push(playersResultObject[key]);
    })

    return {
      players: _.orderBy(playersResult, ['total'], ['desc']),
      teams: _.orderBy(toList(tournament.teams).map((team) => {
        const kills = _.sumBy(team.matches, (match) => _.sumBy(match.teamScore, (score) => parseInt(score.kills)));
        const points = _.sumBy(team.matches, (match) => this.getPositionPoint(match.position));
        return {
          teamId: team.id,
          players: team.players,
          kills: kills,
          damage: _.sumBy(team.matches, (match) => _.sumBy(match.teamScore, (score) => parseInt(score.damage))),
          points: points,
          total: kills + points
        }
      }), ['total'], ['desc'])
    };
  }

}